import { DELETE_ITEM, ADD_ITEM } from "./action";
import { v4 } from "uuid";
const initalState = {
  list: [
    { id: 1, title: "do a list" },
    { id: 2, title: "check list" }
  ]
};

export default function (state = initalState, action) {
  switch (action.type) {
    case DELETE_ITEM: {
      return {
        list: state.list.filter((item) => item.id !== action.payload.id)
      };
    }
    case ADD_ITEM: {
      return Object.assign({}, state, {
        list: [
          {
            id: v4(),
            title: action.payload.text
          },
          ...state.list
        ]
      });
    }
    default:
      return state;
  }
}
